<template>
  <div class="home">
    <div class="bg">
      <img v-bind:src="imgUrl"
           alt="">
      <p class="yuan">
        <span>{{yuan}}</span>
        <em></em>
      </p>
      <p class="dun">
        <span>{{dun}}</span>
        <em></em>
      </p>
    </div>
    <ul class="advantage clearfix">
      <li v-for="(item,index) in datas1">
        <img v-bind:src=item.url
             alt="">
        <p>{{item.con}}</p>
      </li>
    </ul>
    <ul class="showTime clearfix">
      <li v-for="(item,index) in datas2"
          :class="index%2 == 0?'fl':'fr'">
        <img v-bind:src=item.url
             alt="">
        <p>{{item.con}}</p>
        <span>{{item.text}}</span>
      </li>
    </ul>
    <div class="introduction">
      <div class="clearfix w1180">
        <div class="left"
             v-for="(item,index) in datas3"
             v-show="num==index">
          <span>{{item.title}}</span>
          <p>
            {{ item.con1}}
            </br>
            </br>
            </br>
            {{ item.con2}}
          </p>
        </div>
        <div class="right"></div>
      </div>

    </div>
  </div>
</template>

<script>

export default {
  name: 'home',
  data () {
    return {
      num: 0,
      imgUrl: '/static/bg.png',
      yuan: 0,
      dun: 0,
      datas1: [
        { con: '', url: '/static/img5.png' },
        { con: '', url: '/static/img6.png' },
        { con: '', url: '/static/img7.png' },
        { con: '', url: '/static/img8.png' }
      ],
      datas2: [
        { con: '', text: '    ', url: '/static/img2.png' },
        { con: '', text: '    ', url: '/static/img4.png' },
        { con: '', text: '  ', url: '/static/img3.png' },
        { con: '', text: '  ', url: '/static/img1.png' }
      ],
      datas3: [
        {
          title: 'SINOCHEM INTERNATIONAL OIL(SINGAPORE) PTE.LTD',
          con1: '19908AOTGTB',
          con2: ''
        },
        {
          title: '',
          con1: '19882000122004',
          con2: '5002008'
        },
        {
          title: '',
          con1: '201621820185172018 81 ',
          con2: '196116015001997'
        },
        {
          title: 'Freepoint Commodities Singapore Pte Ltd',
          con1: '',
          con2: ''
        },
        {
          title: '',
          con1: '1994012224',
          con2: ''
        },
        {
          title: '',
          con1: '1982,',
          con2: '199711BSIISO9000,20115000150'
        },
        {
          title: '',
          con1: '--- 901200604301000',
          con2: '12'
        },
        {
          title: '',
          con1: '19970506',
          con2: '(Inspectorate Group)150IFIAIP1005800150,'
        },
        {
          title: '',
          con1: '4000',
          con2: ' 191200300'
        },
        {
          title: '',
          con1: '500',
          con2: '93#93#97#0#-10#-20#70#A90#A'
        },
        {
          title: 'Societe Generale de Surveillance S.A. ',
          con1: 'SGS  Societe Generale de Surveillance S.A. SGS',
          con2: 'SGS18781500 75000143'
        },
        {
          title: 'ChinaCertification&InspectionGroupCo.,Ltd.',
          con1: 'CCICChinaCertification&InspectionGroupCo.,Ltd.,CCIC',
          con2: 'WTO'
        }
      ]
    }
  },
  components: {

  },
  watch: {

  },
  created () {
    window.qq = this
    var thef = this
    var timer1 = setInterval(function () {
      thef.yuan++
      if (thef.yuan == 86) {
        clearInterval(timer1);
      }
    }, 10);
    var timer2 = setInterval(function () {
      thef.dun++
      if ( thef.dun == 190) {
        clearInterval(timer2);
      }
    }, 10);
  },
  methods: {
  },
  mounted () {
    var entries = [
      { label: 'Sinochemsg' },
      { label: '' },
      { label: 'CSETMARKET' },
      { label: 'Freepoint' },
      { label: '' },
      { label: '' },
      { label: '' },
      { label: 'INSPECTORATE' },
      { label: '' },
      { label: '' },
      { label: 'SGS' },
      { label: 'CCIC' }
    ];

    var settings = {
      entries: entries,
      width: 370,
      height: 340,
      radius: '30%',
      radiusMin: 100,
      bgDraw: true,
      bgColor: '#292929',
      opacityOver: 1.00,
      opacityOut: 0.6,
      opacitySpeed: 1,
      fov: 700,
      speed: 0.5,
      fontFamily: 'Oswald, Arial, sans-serif',
      fontSize: '18',
      fontColor: '#f36523',
      fontWeight: 'normal',//bold
      fontStyle: 'normal',//italic 
      fontStretch: 'normal',//wider, narrower, ultra-condensed, extra-condensed, condensed, semi-condensed, semi-expanded, expanded, extra-expanded, ultra-expanded
      fontToUpperCase: false
    };
    $('.right').svg3DTagCloud(settings);
  }
}
</script >

<style scoped>
.introduction {
  height: 320px;
  background: #292929;
  font-size: 12px;
  color: #fff;
  overflow: hidden;
  padding-bottom: 20px;
}
.introduction .left {
  width: 760px;
  float: left;
}
.introduction .left span {
  font-size: 24px;
  padding: 50px 0 15px;
  display: block;
}
.introduction .right {
  float: right;
  width: 370px;
  height: 340px;
}
.showTime {
  width: 940px;
  margin: 0 auto 30px;
  color: #fff;
}
.showTime li {
  position: relative;
  padding-bottom: 15px;
}
.showTime li p {
  width: 250px;
  text-align: center;
  position: absolute;
  left: 110px;
  top: 88px;
  font-size: 26px;
}
.showTime li span {
  width: 250px;
  text-align: center;
  position: absolute;
  left: 108px;
  top: 140px;
  font-size: 14px;
}

.advantage {
  width: 940px;
  margin: 50px auto;
}
.advantage li {
  float: left;
  width: 25%;
  text-align: center;
}
.advantage li img {
  padding-bottom: 10px;
}
.bg {
  position: relative;
  color: #fff;
  font-size: 20px;
}
.bg img {
  width: 100%;
}
.yuan {
  position: absolute;
  left: 50%;
  bottom: 40px;
  margin-left: -240px;
  width: 180px;
  text-align: right;
}
.dun {
  position: absolute;
  right: 50%;
  bottom: 40px;
  margin-right: -180px;
  width: 200px;
  text-align: right;
}
.bg p span {
  font-size: 50px;
}
.bg p em {
  font-size: 14px;
  display: block;
  text-align: right;
  margin: -6px 0 0 0;
}
</style>



